.landing-page {
  .landing-header {
    height: 60px;
    box-shadow: 0px 1px 6px #cdcdcf;
    padding: 0px 0px 0px 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .logoimg {
      height: 50px;
      width: 90px;
    }
    .header-ul {
      list-style: none;
      display: flex;
      align-items: center;
      font-size: 20px;
      margin: 0;
      margin-right: 5%;
      font-weight: 540;
      .header-li {
        margin-right: 20px;
        cursor: pointer;
        .linkabout{
            color: #595959;
        }
      }
    }
  }
  .landing-page-section-1 {
    width: 100%;
    height: 550px;
    position: relative;
    margin-top: 5px;
    .landingpage-image {
      height: 100%;
      width: 100%;
    }

    .content-container {
      position: absolute;
      top: 0;
      width: 50%;
      height: 100%;

      .profile-name-desination-container {
        height: 500px;
        width: 100%;
        .ant-row {
          height: 100%;

          .col1 {
            // background: rebeccapurple;
            height: 100%;
            display: flex;
            justify-content: flex-end;
            align-items: center;
          }
          .col2 {
            // background: red;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            .name-designation-holder {
              font-family: "Comic Sans MS", cursive, sans-serif;
              .name {
                font-size: 32px;
              }
              .designation {
                font-size: 22px;
                margin-left: 5px;
              }
            }
          }
        }
      }
    }
  }
  .landing-page-section-2 {
    height: 300px;
    font-family: "Comic Sans MS", cursive, sans-serif;

    .avatar-container-sec2 {
      margin-top: 60px;
      text-align: center;
    }
    .heading-hi {
      text-align: center;
      margin-top: 5px;
      font-size: 30px;
    }
    .sub-heading {
      padding: 10px 30% 0px 30%;
      font-size: 20px;
    }
  }
  .seperatordot-holder {
    text-align: center;
    margin-bottom: 60px;
    .seperatordot {
      height: 32px;
    }
  }
  .landing-page-section-3 {
    min-height: 200px;
    font-family: "Comic Sans MS", cursive, sans-serif;

    .avatar-container-sec3 {
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .sec3-heading {
      font-size: 26px;
      text-align: center;
      margin: 10px;
    }
    .sec3-row {
      height: 100%;
      .sec3-col {
        height: 100%;
        text-align: center;
        .heading-sec3 {
          font-size: 20px;
          margin-bottom: 10px;
        }
        .sep-img {
          height: 8px;
          width: 100px;
          margin-bottom: 20px;
        }
        .sub-heading-sec3 {
          text-align: center;
          padding: 5% 20%;
        }
      }
    }
  }
  .landing-page-section-4 {
    font-family: "Comic Sans MS", cursive, sans-serif;
    height: 350px;
    position: relative;
    .sec4bg {
      height: 300px;
      width: 100%;
      display: none;
    }
    .sec4bg1 {
      height: 100%;
      width: 100%;
    }
    .content-sec4 {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 46%;
      background: #f1f1f1;
      .contact-heading-para {
        font-size: 18px;
        text-align: center;
        padding: 5% 15%;
        //   border-top: 1.5px solid #ececec;
      }
      .contact-heading-para2 {
        font-size: 20px;
        font-weight: 500;
        text-align: center;
      }
      .contact-heading-para3 {
        font-size: 18px;
        font-weight: 500;
        text-align: center;
      }
      .contact-heading-para4 {
        font-size: 18px;
        font-weight: 500;
        text-align: center;
        i {
          margin: 10px;
        }
      }
    }
  }
}

@media (max-width: 1024px) {
  .landing-page {
    .landing-page-section-1 {
      height: 600px;
      .landingpage-image {
        display: block;
      }
      .content-container {
        width: 60%;
        .profile-name-desination-container {
          height: 100%;
          .ant-row {
            .col1 {
              justify-content: center;
            }
          }
        }
      }
    }
  }
}
@media (max-width: 500px) {
  .landing-page {
    .landing-page-section-1 {
      margin-top: 30px;
      height: 400px;
      .landingpage-image {
        display: none;
      }
      .content-container {
        width: 100%;
        height: 200px;
        .profile-name-desination-container {
          .ant-row {
            .col1 {
              justify-content: center;
            }
            .col2 {
            }
          }
        }
      }
    }
    .landing-page-section-2 {
      height: 300px;

      .avatar-container-sec2 {
        margin-top: 10px;
        text-align: center;
      }
      .sub-heading {
        padding: 10px 10% 0px 10%;
        font-size: 20px;
      }
    }
    .landing-page-section-3 {
      .sec3-row {
      }
    }
    .landing-page-section-4 {
      height: 300px;
      .sec4bg {
        display: block;
      }
      .sec4bg1 {
        display: none;
      }
      .content-sec4 {
        left: 0;
        background: transparent;
      }
    }
  }
}

body::-webkit-scrollbar {
  width: 0.3em;
}

body::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

body::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  outline: 1px solid slategrey;
}
